import React, { useEffect, useState } from "react";
import PageLayout from "../../layout";
import { Slider as Slide } from "../../containers";
import { hooks } from "../../hooks";
import Slider from "react-slick";
import Marque from "../../containers/Marque";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";

const Home = () => {
  const posts = hooks.usePost();

  const reversePost = [...posts]?.reverse();

  const [slidesToShow, setSlidesToShow] = useState(3);

  useEffect(() => {
    const handleResize = () => {
      if (window.innerWidth <= 767) {
        setSlidesToShow(1); // 1 slide on mobile
      } else {
        setSlidesToShow(2); // 4 slides on desktop
      }
    };

    // Initial setup
    handleResize();

    // Add event listener for window resize
    window.addEventListener("resize", handleResize);

    // Clean up the event listener
    return () => {
      window.removeEventListener("resize", handleResize);
    };
  }, []);

  const settings = {
    dots: true,
    infinite: true,
    slidesToShow: slidesToShow,
    slidesToScroll: 1,
    autoplay: true,
    speed: 10000,
    autoplaySpeed: 2000,
    cssEase: "linear",
  };

  console.log(posts, "post");
  const alerts = reversePost?.filter((x) => x.type === "alert");

  const featured = reversePost?.filter((x) => x.type === "featured");

  useEffect(() => {
    const scriptId = "c6359d86-9de8-4127-ae5a-99909f0d3919";
    const bannerHeight = 600; // Override banner height
    const bannerWidth = 360; // Override banner width
    const embedType = "inline"; // Set embed type: inline, banner

    const script = document.createElement("script");
    script.defer = true;
    script.src = `https://dmc.partner.microsoft.com/dccn/api/embedded-campaigns/js?id=${scriptId}&height=${bannerHeight}&width=${bannerWidth}&embedType=${embedType}`;

    document.body.append(script);

    // Cleanup: Remove the script when the component unmounts
    return () => {
      document.body.removeChild(script);
    };
  }, []);
  return (
    <PageLayout>
      <Slide posts={featured} />
      <div></div>
      <Slider {...settings}>
        {alerts?.map((item, i) => (
          <Marque post={item} key={i} />
        ))}
      </Slider>
      <div>
        <div id="dccn-c6359d86-9de8-4127-ae5a-99909f0d3919"></div>
      </div>
    </PageLayout>
  );
};

export default Home;
